.portfolio-cards {
  margin: 60px 0 120px;
  @media (max-width: $tablet) {
    margin: 40px 0 100px;
  }
  @media (max-width: $mobile) {
    margin: 20px 0 40px;
  }

  &__items{
    .projects{
      margin-top: 100px;
      padding:0;
      @media (max-width: $tablet){
        margin-top: 80px;
      }
      @media (max-width: $mobile){
        margin-top: 40px;
      }
    }
  }

  &__tabs {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px 80px;
    @media (max-width: $laptop) {
      flex-wrap: wrap;
      margin: 0 -10px 10px;
    }
    @media (max-width: $mobile) {
      margin: 0 -10px 0px;
    }

    .portfolio-tab {
      width: calc((100% / 6) - 40px);
      margin: 0 20px;
      flex-grow: 1;
      @media (max-width: $laptop) {
        width: calc((100% / 3) - 20px);
        margin: 0 10px 75px;
      }
      @media (max-width: $mobile) {
        width: calc((100% / 2) - 20px);
        margin: 0 10px 50px;
      }
    }
  }
}

.portfolio-tab {
  position: relative;
  height: 142px;
  @include hover-effect;

  input {
    display: none;
    &:checked + label{
      .portfolio-tab__title {
        background: #777777;
        color: #fff;
      }
    }
  }


  &__photo {
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    position: absolute;
    left: 9px;
    right: 9px;
    top: 100%;
    transform: translateY(-50%);
    background-color: #fff;
    min-height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 15px;
    font-weight: 500;
    line-height: 18.15px;
    text-transform: uppercase;
    @media (max-width: $mobile) {
      font-size: 13px;
      line-height: 15.73px;
    }
  }

}


.portfolio-top {
  display: flex;
  gap: 16rem;

  &__right {
    min-width: 32rem;
  }

  &__title {
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 2.9rem;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    margin-bottom: 1.4rem;
  }

  &__descr {
    p {
      font-weight: 400;
      font-size: 1.6rem;
      line-height: 1.9rem;
      letter-spacing: 0.03rem;

      &:not(:last-child) {
        margin-bottom: 1rem;
      }
    }
  }

  &__form {
    input {
      margin-bottom: 1.8rem;
      background: #F0F0F0;

      &[type=button] {
        padding: 1rem;
        background: #FFFFFF;
        color: #000000;
        border-radius: 0.5rem;
        cursor: pointer;
        border: 0.1rem solid #555454;
        width: 100%;
      }
    }

    label {
      cursor: pointer;
      display: flex;
      flex-direction: row;
      align-items: center;
      font-weight: 300;
      font-size: 1.3rem;
      line-height: 1.5rem;
      letter-spacing: 0.03rem;
      color: #777777;
      padding-left: 2.5rem;
    }
  }
}

.portfolio-items {
  display: flex;
  flex-wrap: wrap;
  gap: 7rem 3rem;

  &__card {
    width: calc(100% / 3 - 30px);
  }

  &__img {
    margin-bottom: 1rem;
    height: 27.1rem;
    display: block;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__category {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3rem;

    svg {
      margin-right: 0.5rem;
    }

    span {
      font-weight: 700;
      font-size: 1.2rem;
      line-height: 1.5rem;
      letter-spacing: 0.03rem;
      text-transform: uppercase;
      color: #363636;
    }
  }

  &__left, &__right {
    display: flex;
    align-items: center;
  }

  &__title {
    font-weight: 800;
    font-size: 1.8rem;
    line-height: 2.2rem;
    text-transform: uppercase;
    color: #000000;
  }
}

.portfolio-bottom {
  &__wrapper {
    margin-bottom: 7rem;
  }

  &__filter {
    margin-bottom: 3rem;
  }
}


.filter {
  display: flex;
  align-items: center;

  &__caption {
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 1.9rem;
    letter-spacing: 0.03rem;
    text-transform: uppercase;
    color: #232323;
    margin-right: 2.5rem;
  }

  &__button {
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: #000000;

    &:not(:last-child) {
      margin-right: 3rem;
    }

    &--active {
      background: #DBDBDB;
      border: 1px solid #DBDBDB;
      border-radius: 0.5rem;
      padding: 0.6rem 2rem;
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 1.9rem;
      letter-spacing: 0.03rem;
      text-transform: uppercase;
      color: #232323;
    }
  }
}
