.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 17px 34px;
  font-size: 15px;
  font-weight: 600;
  line-height: 15px;
  text-align: left;
  border: 1px solid #555454;
  color: #555454;
  text-transform: uppercase;
  min-width: 190px;

  cursor: pointer;
  transition: background-color .3s;
  @include hover-effect;

  &--border {
    border: 1px solid #555454;
    color: #434343;
    background-color: #FFFFFF;
  }

  &--dark {
    background-color: #555454;
    border: 1px solid #555454;
    color: #fff;
  }
}

a.btn-weight {
  font-weight: 500;
  @include hover-effect;
}


