.product {
  &__container {
    display: flex;
    margin-top: 6rem;
    @media (max-width: 1440px) {
      flex-wrap: wrap;
    }
    @media (max-width: $mobile) {
      margin-top: 25px;
    }
  }

  &__detail {
    display: grid;
    grid-template-columns: 50% 1fr;
    width: 66%;
    flex-shrink: 0;
    @media (max-width: 1440px) {
      width: 100%;
      margin-bottom: 20px;
    }
    @media (max-width: $tablet) {
      grid-template-columns: 54% 1fr;
    }

    &.product__detail--package {
      .product {
        &__gallery {
          grid-column: 1;
          grid-row: 1;
          width: calc(100% - 28px);
          @media (max-width: $mobile) {
            width: 100%;
            grid-column: 1/3;
          }
        }

        &__variants {
          grid-column: 1/3;
          grid-row: 2;
        }
      }
    }
  }

  &__gallery {
    width: 100%;
    grid-row: 1/3;
    margin-right: 2.8rem;
    @media (max-width: $mobile) {
      grid-column: 1/3;
      grid-row: 1;
      margin-right: 0;
    }

    .product__slider {
      padding: 5rem 0 9.5rem;
      overflow: hidden;
      position: relative;

      @media (max-width: $mobile) {
        padding-top: 0;
        padding-bottom: 32px;
      }

      .swiper-slide {
        img {
          display: block;
          max-width: 100%;
          margin: 0 auto;
        }
      }

      .swiper-prev, .swiper-next {
        display: none;
      }
    }

    .product__thumbs {

      overflow: hidden;
      position: relative;

      .swiper-container {
        &::after {
          content: '';
          background: linear-gradient(269.64deg, #FFFFFF 31.16%, rgba(255, 255, 255, 0) 129.37%);
          height: 100%;
          width: 100px;
          right: 0;
          top: 0;
          z-index: 1;
          display: block;
          position: absolute;
        }

        &.reached-end:after {
          content: none;
        }
      }

      .nav-prev,
      .nav-next {
        position: absolute;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        @media (max-width: $mobileMedium) {
          display: none;
        }

        &.swiper-button-disabled {
          display: none;
        }
      }

      .nav-prev {
        left: 40px;
        @media (max-width: 1440px) {
          left: 20px;
        }
        @media (max-width: $tablet) {
          left: 0px;
        }
      }

      .nav-next {
        right: 40px;
        @media (max-width: 1440px) {
          right: 20px;
        }
        @media (max-width: $tablet) {
          right: 0px;
        }
      }

      .swiper-slide {
        width: 12.3rem;
        height: 13rem;
        padding: 1rem;
        border: 1px solid #CDCDCD;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        @media (max-width: $tablet) {
          height: 10vw;
        }
        @media (max-width: $mobile) {
          width: 66px;
          height: 66px;
        }

        img {
          display: block;
          margin: 0 auto;
          width: auto;
          max-width: 100%;
          max-height: 100%;
          object-fit: contain;
        }
      }


    }
  }

  &__favorite {
    display: flex;
    align-items: center;
    margin-right: 5rem;
    @media (max-width: $tablet) {
      font-size: 14px;
      line-height: 16.94px;
      margin-right: auto;
    }
  }

  &__actions {
    display: flex;
    margin-bottom: 3rem;

    span {
      margin-left: 1.5rem;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 2.9rem;
    letter-spacing: 0.03rem;
    color: #232323;
    margin-bottom: 1rem;
  }

  &__propertes {
    margin-top: 3rem;
    letter-spacing: 0.03rem;
    @media (max-width: $mobile) {
      margin-top: 20px;
    }

    .propertes__item {
      margin-bottom: 1rem;
    }

    .propertes__item-name {
      font-weight: 600;
      margin-right: 0.5rem;
    }

    .propertes__item-value {
      margin-left: 0.5rem;
    }
  }

  &__info {
    padding-top: 0.5rem;
    @media (max-width: $mobile) {
      grid-column: 1/3;
      margin-top: 25px;
    }

    .product__availability {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 3rem 0;

      .product__price {
        font-weight: 700;
        font-size: 3.6rem;
        line-height: 4.4rem;
        letter-spacing: 0.03rem;
        color: #000000;
        @media (max-width: $tablet) {
          font-size: 24px;
          line-height: 29.05px;
        }
        @media (max-width: $mobile) {
          font-size: 18px;
          line-height: 21.78px;
        }
      }

      .product__stocks {
        margin-left: 3rem;
        font-size: 1.4rem;
        line-height: 1.7rem;
        letter-spacing: 0.03rem;

        .stocks-status {
          padding-left: 1.5rem;
          position: relative;

          &:before {
            content: "";
            display: block;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            position: absolute;
            left: 2.5px;
            top: 50%;
            transform: translateY(-50%);
          }

          &.in_stocks {
            &:before {
              background: #0FC100;
            }
          }

          &.out_stocks {
            &:before {
              background: #FF9900;
            }
          }
        }
      }
    }

    .product__btns {
      display: flex;
      margin-bottom: 1rem;
      @media (max-width: $mobileMedium) {
        flex-wrap: wrap;
      }

      .product__btns-block {
        @media (max-width: $mobileMedium) {
          width: 100%;
        }

        &:first-child {
          margin-right: 2.4rem;
          @media (max-width: $tablet) {
            margin-right: 20px;
          }
          @media (max-width: $mobileMedium) {
            margin-right: 0;
            margin-bottom: 13px;
          }
        }

        @media (max-width: $tablet) {
          flex-grow: 1;
          a, button {
            height: 100%;
            width: 100%;
            padding-left: 5px;
            padding-right: 5px;
            min-width: unset;
          }
        }
      }
    }

    .product__services {
      margin: 1rem 0 2rem;

    }

  }

  &__variants {
    grid-column: 2;
    overflow: hidden;
    margin-top: 1rem;
    @media (max-width: $tablet) {
      grid-column: 1/3;
      grid-row: 3;
      margin-top: 35px;
    }

    .product__variants-title {
      font-weight: 700;
      letter-spacing: 0.03rem;
      text-transform: uppercase;
      color: #232323;
    }

    .product__variants-list {
      overflow: hidden;
      position: relative;
      padding-top: 1.5rem;

      .swiper-container {
        position: relative;

        &::after {
          content: '';
          background: linear-gradient(269.64deg, #FFFFFF 31.16%, rgba(255, 255, 255, 0) 129.37%);
          height: 100%;
          width: 100px;
          right: 0;
          top: 0;
          z-index: 1;
          display: block;
          position: absolute;
        }

        &.reached-end:after {
          content: none;
        }
      }

      .nav-prev,
      .nav-next {
        position: absolute;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        @media (max-width: $mobileMedium) {
          display: none;
        }

        &.swiper-button-disabled {
          display: none;
        }
      }

      .nav-prev {
        left: 26px;
      }

      .nav-next {
        right: 26px;
      }
    }

    .product__variants-item {
      max-width: 14rem;
      padding: 1.2rem 0.7rem;
      letter-spacing: 0.03rem;
      text-align: center;
      border: 1px solid #D5D5D5;
      border-radius: 5px;

      img {
        max-height: 9.4rem;
        display: block;
        margin: 0 auto;
      }

      .product__variants-item-title {
        margin: 2rem 0 1rem;
        min-height: 3.9rem;
      }

      .product__variants-item-price {
        font-weight: 700;
        color: #000000;
      }
    }
  }


  &__compare {
    display: flex;
    align-items: center;
    @media (max-width: $tablet) {
      font-size: 14px;
      line-height: 16.94px;
    }
  }

  &__additional {
    display: flex;
    margin-left: 3.8rem;
    @media (max-width: 1440px) {
      flex-direction: column;
      margin-left: 0;
      width: 100%;
    }

    .product__additional-title {
      position: relative;
      z-index: 2;
      background: #555454;
      border-radius: 5px;
      text-align: center;
      font-weight: 700;
      text-align: center;
      letter-spacing: 0.03rem;
      text-transform: uppercase;
      padding: 1.1rem;
      color: #FFFFFF;
      margin-bottom: 1.7rem;
      @media (max-width: 1440px) {
        display: inline-block;
        align-self: flex-start;
      }
      @media (max-width: $tablet) {
        background-color: unset;
        color: #232323;
        padding: 0;
      }
    }

    .product__recommeded {
      position: relative;

      @media (max-width: 1440px) {
        &::after,
        &::before {
          content: '';
          top: 0;
          z-index: 1;
          display: block;
          position: absolute;
          height: calc(100% - 5px);
          width: calc(50vw - 50%);
        }

        &::before {
          left: calc(50% - 50vw);
          background: linear-gradient(269.64deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
        }

        &::after {
          right: calc(50% - 50vw);
          background: linear-gradient(269.64deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
        }
      }

      @media (max-width: $tablet) {
        margin-top: 60px;
      }

    }

  }


  &__tabs-box-header {
    display: flex;
  }

  &__services-field {
    margin-top: 17px;

    label {
      padding-left: 5rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      @media (max-width: $tablet) {
        padding-left: 36px;
      }
    }

    input {
      display: none;
    }

    input[type="checkbox"] + label {
      position: relative;
      cursor: pointer;

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        display: inline-block;
        border-radius: 5px;
        border: 0.5px solid #000000;
        width: 3rem;
        height: 3rem;
      }
    }

    input[type="checkbox"]:checked + label {
      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        left: 7px;
        top: 50%;
        transform: translateY(-50%);
        width: 2rem;
        height: 2rem;
        background-image: url("data:image/svg+xml;utf8,<svg width='17' height='17' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M9.56843 0.381832C9.47664 0.322258 9.37401 0.281352 9.26641 0.261451C9.15881 0.24155 9.04834 0.243044 8.94131 0.265847C8.83428 0.28865 8.7328 0.332316 8.64265 0.39435C8.55251 0.456385 8.47547 0.535571 8.41593 0.627387L3.74649 7.82489L1.46926 5.71933C1.38892 5.64504 1.29473 5.58731 1.19208 5.54942C1.08942 5.51153 0.980305 5.49424 0.870964 5.49852C0.65014 5.50717 0.441796 5.60318 0.291764 5.76544C0.141733 5.9277 0.0623038 6.14292 0.0709514 6.36374C0.0795989 6.58457 0.175614 6.79291 0.337875 6.94294L3.32788 9.70739C3.32788 9.70739 3.41371 9.78128 3.45343 9.80711C3.54523 9.86673 3.64788 9.90767 3.75551 9.92759C3.86315 9.94752 3.97365 9.94603 4.08071 9.92323C4.18777 9.90043 4.28928 9.85674 4.37945 9.79469C4.46962 9.73263 4.54667 9.65341 4.60621 9.56155L9.81399 1.53433C9.87356 1.44254 9.91447 1.33991 9.93437 1.23231C9.95427 1.12471 9.95278 1.01424 9.92997 0.90721C9.90717 0.800184 9.8635 0.698699 9.80147 0.608553C9.73943 0.518407 9.66025 0.441366 9.56843 0.381832Z' fill='black'/></svg>");
        background-repeat: no-repeat;
      }
    }
  }

}

.recommended-list {
  position: relative;
  max-height: 67rem;
  padding: 0 7px;
  overflow-y: auto;
  @media (max-width: 1440px) {
    max-height: unset;
    display: flex;
    overflow-x: auto;
    margin-right: calc(50% - 50vw);
    padding-right: calc(50vw - 50%);
    margin-left: calc(50% - 50vw);
    padding-left: calc(50vw - 50%);
    padding-bottom: 5px;
  }

  &__item {
    max-width: 18.9rem;
    height: 15.5rem;
    padding: 0 3rem 0.5rem;
    margin-bottom: 1.7rem;
    border: 1px solid #555454;
    border-radius: 5px;
    @media (max-width: 1440px) {
      margin-bottom: 0;
    }

    & + .recommended-list__item {
      @media (max-width: 1440px) {
        margin-left: 20px;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    img {
      display: block;
      margin: 0 auto 0.5rem;
      width: auto;
      max-height: 12.2rem;
    }

    span {
      display: block;
      text-align: center;
      font-weight: 800;
      font-size: 1.4rem;
      line-height: 1.7rem;
      text-transform: uppercase;
      color: #000000;
    }
  }
}

.product-similar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 4rem;
  border-radius: 5px;
  overflow: hidden;

  @media (max-width: 1440px) {
    position: relative;
    margin-left: 0;
    align-items: unset;
    overflow: visible;
    margin-top: 20px;
    &::after,
    &::before {
      content: '';
      top: 0;
      z-index: 1;
      display: block;
      position: absolute;
      height: calc(100% - 5px);
      width: calc(50vw - 50%);
    }

    &::before {
      left: calc(50% - 50vw);
      background: linear-gradient(269.64deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
    }

    &::after {
      right: calc(50% - 50vw);
      background: linear-gradient(269.64deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
    }
  }

  @media (max-width: $tablet) {
    margin-top: 60px;
  }

  &__list {
    border: 1px solid #555454;
    border-radius: 5px;
    max-height: 67rem;
    overflow-y: auto;
    padding: 2rem 2rem 0;
    @media (max-width: 1440px) {
      max-height: unset;
      display: flex;
      overflow-x: auto;
      margin-right: calc(50% - 50vw);
      padding-right: calc(50vw - 50%);
      margin-left: calc(50% - 50vw);
      padding-left: calc(50vw - 50%);
      padding-top: 0;
      padding-bottom: 5px;
      border: none;

    }
  }

  &__prop {
    &--brand {
      font-weight: 800;
    }

    &--title {
      font-weight: 500;
      margin-bottom: 1rem;
    }
  }

  &__item {
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 1.7rem;
    letter-spacing: 0.03rem;
    color: #000000;
    min-width: 13rem;
    margin-bottom: 2rem;

    @media (max-width: 1440px) {
      & + .product-similar__item {
        margin-left: 20px;
      }
    }


    img {
      max-width: 13rem;
      border: 1px solid #C4C4C4;
      border-radius: 5px;
      overflow: hidden;
      margin-bottom: 0.5rem;
    }

    span {
      display: block;
    }
  }
}


.product-info {
  margin-top: 100px;

  &__tabs-nav {
    display: flex;
    overflow-x: auto;
  }

  &__content-wrapper {
    padding: 60px 0 120px;
    @media (max-width: $tablet) {
      padding: 40px 0 100px;
    }
    @media (max-width: $mobile) {
      padding: 20px 0 50px;
    }
  }

  .section-header {
    align-items: flex-start;

    &__desc {
      max-width: 1362px;

      p + p {
        margin-top: 1em;
      }
    }
  }


  &__tabs-item {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    flex-grow: 1;
    padding: 2.9rem 0;
    cursor: pointer;

    @media (max-width: $tablet) {
      padding: 18px 12px;
      white-space: nowrap;

    }

    &[data-active] {
      font-weight: 800;
      color: #212121;
      background: #F5F5F5;
    }

    &:hover {
      color: #212121;
      background: #F5F5F5;
    }
  }

  &__tabs {
    background: #363636;


    .product__tabs-container {
      padding: 6rem 10rem;

      .product__tabs-box-title {
        margin-right: 11.6rem;

        .product__name {
          letter-spacing: 0.25rem;
          text-transform: uppercase;
          color: #939090;
          margin-bottom: 1rem;
        }

        .product__tabs-title {
          font-size: 4.2rem;
          line-height: 5.1rem;
          letter-spacing: 0.03rem;
          text-transform: uppercase;
          color: #181818;
        }
      }

      .product__description {
        letter-spacing: 0.03rem;

        p {
          margin-bottom: 1.5rem;
        }
      }

      .product__tabs-box-content {
        margin: 6rem 0 4rem;

        .product__description-img {
          display: flex;
          column-gap: 4rem;
        }
      }
    }
  }

  &__content-block {
    display: none;

    &[data-active] {
      display: block;
    }
  }
}

.description-block {
  &__slider {
    .swiper-pagination-circles {
      display: none;
      @media (max-width: $tablet) {
        display: flex;
        margin-top: 12px;
      }
    }

    @media (max-width: $tablet) {
      position: relative;
      margin: 0 calc(50% - 50vw);
    }

    .swiper-slide {
      @media (max-width: $tablet) {
        width: 208px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.specifications {
  &__list {
    li {
      display: flex;
      justify-content: space-between;
      padding: 14px 12px;
      font-size: 18px;
      font-weight: 500;
      line-height: 21.78px;
      @media (max-width: $mobile) {
        font-size: 16px;
        line-height: 18.78px;
      }
      @media (max-width: $mobileMedium) {
        font-size: 14px;
        line-height: 16.78px;
      }

      &:nth-child(2n - 2) {
        background: #F2F2F2;
      }

      span {
        width: calc(50% - 10px);
        flex-shrink: 0;
      }
    }
  }
}

.instructions {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -50px;
  max-width: 1100px;
  @media (max-width: $tablet) {
    margin: -15px -30px;
  }
  @media (max-width: $mobile) {
    margin: -10px;
    justify-content: space-between;
  }
  @media (max-width: $mobileMedium) {
    margin: 0 0 -10px;
  }

  .file-link {
    margin: 20px 50px;
    @media (max-width: $tablet) {
      margin: 15px 30px;
    }
    @media (max-width: $mobile) {
      width: calc(50% - 20px);
      margin: 10px 10px;
    }
    @media (max-width: $mobileMedium) {
      width: 100%;
      margin: 0 0 10px;
    }
  }
}

.rewards-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: -60px;
  @media (max-width: $tablet) {
    margin-bottom: -20px;
  }

  .reward-item {
    width: calc(50% - 40px);
    margin: 0 0 60px;
    @media (max-width: $tablet) {
      width: 100%;
      margin: 0 0 20px;
    }
  }
}


.review-item {
  &__title {
    font-size: 24px;
    font-weight: 500;
    line-height: 28.8px;
    margin-bottom: 19px;
    @media (max-width: $mobile) {
      font-size: 22px;
      line-height: 26px;
    }

    span {
      font-size: 18px;
      font-weight: 400;
      line-height: 27px;
      color: #454545;
      @media (max-width: $mobile) {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }

  &__descr {
    font-family: "Raleway";
    font-size: 15px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0.30000001192092896px;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 23px;
    }
  }

  &__stars {
    margin-bottom: 12px;
  }
}

.star {
  position: relative;
  display: inline-block;
  width: 17px;
  height: 16px;

  &::before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
