.reviews-block {
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: -60px;
    @media (max-width: $tablet) {
      margin-bottom: -40px;
    }

    .review-item {
      width: calc(50% - 40px);
      margin: 0 0 60px;
      @media (max-width: $tablet) {
        width: calc(50% - 20px);
        margin: 0 0 40px;
      }
      @media (max-width: $mobile) {
        width: 100%;
      }
    }
  }

  &__btn {
    margin-bottom: 68px;
    @media (max-width: $mobile) {
      margin-bottom: 40px;
    }
  }
}

.package-gallery {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #C4C4C4;
  border-radius: 10px;
  overflow: hidden;
  max-height: 600px;
  width: 100%;

  &__item {
    position: relative;
    width: calc(100% / 3);
    flex-shrink: 0;
    flex-grow: 1;
    min-height: 33%;
    border: 1px solid #C4C4C4;
  }

  &__name-label {
    position: absolute;
    background: #FFFFFFCC;
    color: #363636;
    left: 50%;
    transform: translateX(-50%);
    top: 5px;
    z-index: 1;
    width: max-content;
    max-width: 90%;
    font-size: 14px;
    font-weight: 400;
    line-height: 16.94px;
    padding: 4px;
  }

  &__photo {
    position: relative;
    width: 100%;
    height: 100%;

    &--no-photo {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
      max-height: 146px;
      @media (max-width: $laptop) {
        max-height: 17vw;
        min-height: 137px;
      }
      @media (max-width: $mobile) {
        height: 30vw;
        max-height: unset;
      }
    }
  }
}