

.catalog {
  margin-bottom: 100px;
  @media(max-width: $tablet) {
    margin-bottom: 80px;
  }
  @media(max-width: $mobile) {
    margin-bottom: 60px;
  }

  &__pagination {
    margin-top: 60px;
    @media(max-width: $tablet) {
      margin-top: 40px;
    }
    @media(max-width: $mobile) {
      margin-top: 20px;
    }
  }

  &__products {
    margin-top: 60px;
    @media(max-width: $tablet) {
      margin-top: 40px;
    }
    @media(max-width: $mobile) {
      margin-top: 20px;
    }
  }

  &__filter-box {
    margin-bottom: 3rem;


    .filter-box__title {
      background: #D3D3D3;
      padding: 1rem 1.5rem;
      border-radius: 5px 5px 0px 0px;
      font-weight: 700;
      text-transform: uppercase;
    }

    .filter-box__cont {
      background: #F5F5F5;
      border-radius: 0px 0px 5px 5px;
    }
  }
}

.catalog-sort {
  display: flex;
  position: relative;
  z-index: 2;

  &__label {
    margin-right: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 19.36px;
    text-transform: uppercase;
  }

  &__item-to-choose {
    white-space: nowrap;
    padding: 5px;
    cursor: pointer;
    @include hover-effect;

    &[data-active] {
      cursor: auto;
      pointer-events: none;
      font-weight: 700;
    }

    &--active {
      &:hover {
        opacity: 1;
      }
    }
  }

  &__body {
    position: absolute;
    top: calc(100% + 10px);
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    background: #fff;
    min-width: 100%;
    padding: 5px;
    font-size: 15px;
    border-radius: 0 0 10px 10px;
    border: 1px solid #10101047;

    &[data-active] {
      z-index: 1;
      visibility: visible;
      opacity: 1;

    }
  }

  &__current {
    cursor: pointer;
  }
}

.catalog-products {
  &__wrapper {
    display: flex;
  }

  &__filter {
    display: none;
    @media (max-width: $tablet) {
      position: relative;
      display: inline-flex;
      align-items: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 16.94px;
      padding: 10px;
      margin-left: -10px;
      cursor: pointer;
      @include hover-effect;
      &::before {
        content: '';
        background: #545454;
        width: 10px;
        height: 10px;
        display: inline-block;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px -50px;
    @media (max-width: $mobileMedium) {
      margin: 0 0 -30px;
    }

    .product-card {
      width: calc(33% - 40px);
      margin: 0 20px 50px;
      @media (max-width: $laptop) {
        width: calc(50% - 40px);
        margin: 0 20px 40px;
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin: 0 0 30px;
        &__img {

        }
      }
    }
  }


  &__sort {
    display: flex;
    @media (max-width: $tablet) {
      width: 100%;
      order: 2;
      margin-top: 30px;
    }
  }

  &__sidebar {
    width: 317px;
    flex-shrink: 0;
    margin-right: 40px;

  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #D9D9D9;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
  }

  &__content {
    flex-grow: 1;
  }
}

.filter__brands-result {
  .arrow-checkbox {
    &:nth-child(n+ 7) {
      display: none;
    }
  }

  &[data-full] {
    .arrow-checkbox {
      &:nth-child(n+ 7) {
        display: block;
      }
    }
  }
}

.filter__brands {
  padding: 2rem 1.5rem;

  .filter__brands-preset {
    margin-left: -0.7rem;
    margin-right: -0.7rem;
    margin-bottom: 2rem;

    .brands-preset-item {
      font-size: 1.5rem;
      line-height: 1.8rem;
      color: #9B9B9B;
      display: inline-block;
      margin: 0 0.7rem 0.5rem;
      cursor: pointer;

      &.active {
        font-weight: 700;
        color: #000;
      }

      &:hover {
        color: #000;
      }
    }
  }


  .brands-list-more {
    display: block;
    font-weight: 700;
    letter-spacing: 0.03rem;
    color: #000000;
    cursor: pointer;
    margin: 1rem 0;
  }
}

.filter-box__cont {
  .filter__price {
    padding: 2rem 1.5rem;

    .filter-input-raw {
      margin-bottom: 2.3rem;

      input[type="text"] {
        max-width: 13.5rem;
      }
    }

    .slider {
      height: 5px;
      position: relative;
      background: #ddd;
      border-radius: 5px;
    }

    .slider .progress {
      height: 100%;
      left: 0%;
      right: 0%;
      position: absolute;
      border-radius: 5px;
      background: linear-gradient(90deg, #EAEAEA 0%, #3A3A3A 100.68%);
    }

    .range-input {
      position: relative;
      margin-bottom: 1rem;
    }

    .range-input input {
      position: absolute;
      width: 100%;
      height: 5px;
      top: -5px;
      background: none;
      pointer-events: none;
      -webkit-appearance: none;
      -moz-appearance: none;
    }

    input[type="range"]::-webkit-slider-thumb {
      width: 2rem;
      height: 2rem;
      border-radius: 50%;
      background: #FFFFFF;
      pointer-events: auto;
      -webkit-appearance: none;
      box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
      border: 0.5rem solid #000;
      cursor: pointer;
    }

    input[type="range"]::-moz-range-thumb {
      width: 1rem;
      height: 1rem;
      border: none;
      border-radius: 50%;
      background: #FFFFFF;
      pointer-events: auto;
      -moz-appearance: none;
      box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
      border: 0.5rem solid #000;
      cursor: pointer;
    }

  }
}

.filter-group__block {
  padding: 2rem 1.5rem 0;
  border-bottom: 1px solid #fff;


  .filter-group__block-name {
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 1.8rem;
    text-transform: uppercase;
    color: #000000;
    position: relative;
    cursor: pointer;


    .spoiler {
      width: 1.6rem;
      height: 0.2rem;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      background: #000;

      &:after {
        content: "";
        display: block;
        position: absolute;
        right: 0.7rem;
        top: 50%;
        transform: translateY(-50%);
        width: 0.2rem;
        height: 1.6rem;
        background: #000;
      }
    }
  }


  .filter-group__block-list {
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: all 0.3s ease;
    padding: 0rem 0rem 2rem 2.5rem;

    .filter-prop-checkbox__label {
      font-size: 14px;
      line-height: 16px;
    }

    .filter-prop__item {
      margin-bottom: 1rem;

      .filter-prop-input-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $mobile) {
          flex-wrap: wrap;
        }

        .filter-prop-input {
          width: calc(50% - 8px);
          flex-shrink: 0;
          @media (max-width: $mobile) {
            flex-direction: column;
            align-items: flex-start;
          }
        }

        input[type="text"] {

          &:focus {
            border: 0.5px solid #A2A2A2;
          }
        }
      }

      .filter-prop__item-name {
        margin-bottom: 0.9rem;
      }

      .filter-prop-input-text {
        font-size: 1.2rem;
        line-height: 1.5rem;
        margin-right: 1rem;
        @media (max-width: $mobile) {
          margin-right: 0;
          margin-bottom: 10px;
        }
      }

      .filter-prop-input {
        display: flex;
        align-items: center;

        input[type="text"] {
          width: inherit;
          flex-grow: 1;
          height: 2.8rem;
          min-height: auto;
          border: none;
          border-radius: 0.05rem;
          font-size: 1.2rem;
          line-height: 1.5rem;
          padding: 7px 10px;
          @media (max-width: $mobile) {
            width: 100%;
          }
        }
      }

    }
  }

  &--active {
    .filter-group__block-name {
      .spoiler:after {
        content: none;
      }
    }

    .filter-group__block-list {
      opacity: 1;
      height: 100%;
      padding: 1.5rem 0rem 2rem 2.5rem;
      @media (max-width: $mobile) {
        padding: 10px 0;
      }
    }
  }
}

.filter-single__block {
  padding: 0 1.5rem;
  border-bottom: 1px solid #fff;

  .filter-prop-checkbox__label {
    padding: 20px 0;
  }
}

