    }
  }

  &__label {
    position: relative;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 1.8rem;
    text-transform: uppercase;
    color: #000000;
    width: 100%;
    display: block;
    padding: 7px 0;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      width: 20px;
