      border: 1px solid #D0D0D0;
    }
  }
}

.catalog__list-top {
  margin-bottom: 1.8rem;
}

.catalog__layout-btn {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding: 1rem;
  border: 0.5px solid #070707;
  border-radius: 3px;

  &.active {
    svg {
      rect {
        fill: #3A3A3A;
      }

      path {
        fill: #3A3A3A;
      }
    }
  }
}


.catalog__list-item {
  max-width: 31.7rem;
  margin-bottom: 5rem;

  .product-card__img {
    display: block;
    position: relative;
    border: 1px solid #C4C4C4;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 1rem;

    img {
      width: 31.7rem;
    }

    .product-card__brand-logo {
      position: absolute;
      top: 0.5rem;
      left: 0.5rem;
      background: rgba(255, 255, 255, 0.9);
      padding: 0.5rem 1.5rem;
      z-index: 1;

      img {
        width: 100%;
      }
    }
  }

  .product-card__content {
    .product-card__brand {
      font-weight: 800;
      font-size: 1.8rem;
      line-height: 2.2rem;
      text-transform: uppercase;
      color: #000000;
    }

    .product-card__title {
      display: block;
      font-weight: 500;
      text-transform: uppercase;
      color: #000000;
      margin: 1rem 0 0.5rem;
    }

    .product-card__desc {
      letter-spacing: 0.03rem;
    }

    .product-card__actions {
      margin: 3rem 0;

      .product-card__compare {
        margin-right: 2.7rem;

        span {
          font-size: 1.4rem;
          line-height: 1.7rem;
          letter-spacing: 0.03rem;
          margin-left: 0.9rem;
        }
      }

      .product-card__favorite {
        span {
          font-size: 1.4rem;
          line-height: 1.7rem;
          letter-spacing: 0.03rem;
          margin-left: 0.9rem;
        }
      }
    }

    .product-card__availability {
      min-height: 3.4rem;

      .product-card__price {
        min-width: 51%;
        font-weight: 700;
        font-size: 2.4rem;
        line-height: 2.9rem;
        letter-spacing: 0.03rem;
        color: #000000;
      }

      .product-card__stocks {
        margin-left: 3rem;
        font-size: 1.4rem;
        line-height: 1.7rem;
        letter-spacing: 0.03rem;

        .stocks-status {
          padding-left: 1.5rem;
          position: relative;

          &:before {
            content: "";
            display: block;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            position: absolute;
            left: 2.5px;
            top: 50%;
            transform: translateY(-50%);
          }

          &.in_stocks {
            &:before {
              background: #0FC100;
            }
          }

          &.out_stocks {
            &:before {
              background: #FF9900;
            }
          }
        }
      }
    }

    .product-card__btns {
      margin-top: 1.5rem;
    }
  }
}


.catalog__sections {

}


