  margin-bottom: 1rem;

  input {
    display: none;

    &:checked + label {
      &::before {
        content: '';
        width: 10px;
        height: 10px;
        position: absolute;
        left: -20px;
        top: 50%;
        transform: translateY(-50%);
        display: inline-block;
