  label {
    position: relative;
    font-size: 1.5rem;
    line-height: 1.8rem;
    cursor: pointer;
    color: #000;
    @include hover-effect;

    &:before {
      display: none;
    }
  }
}

.brands-result-list {
  margin-bottom: 2rem;
  padding-left: 20px;
}


.catalog-sidebar {

  &__btns {
    @media (max-width: $tablet) {
      order: -1;
      margin-bottom: 50px;
    }

    .btn {
      width: 100%;
    }
  }

  @media (max-width: $tablet) {
    display: none;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 100;
    width: 50%;
    background: #fff;
    margin-right: 0;
    padding: 0 50px;
    overflow-y: auto;
    min-width: 484px;
    &[data-opened] {
      display: flex;
    }
  }
  @media (max-width: $mobile) {
    min-width: 100vw;
    width: 100vw;
    padding: 0 25px;
  }

  &__top {
    display: none;
    @media (max-width: $tablet) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 20px 0 10px;
      font-size: 16px;
      font-weight: 500;
      line-height: 19.36px;
      order: -1;
    }
  }

  &__close {
    display: flex;
    justify-content: center;
    align-items: center;
  }

}