.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #D9D9D9 !important;
}

.swiper-pagination-bullet {
  width: 5rem !important;
  height: 0.4rem !important;

  @media (max-width: 1300px) {
    width: 2.5rem !important;
    height: 0.2rem !important;
  }
}

.swiper-pagination {
  position: unset !important;
}

.swiper-pagination-bullet {
  background: rgba(208, 208, 208, 0.4) !important;
  opacity: unset !important;
  border-radius: 0% !important;
}

.slider__navigation {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.slider__button-prev {
  position: absolute;
  left: rem(190);
  bottom: rem(280);

  @media (max-width: 1600px) {
    left: rem(100);
  }

  @media (max-width: 1440px) {
    left: rem(50);
  }

  @media (max-width: 1320px) {
    left: rem(100);
    bottom: rem(240);
  }

  @media (max-width: 1280px) {
    left: rem(50);
  }

  @media (max-width: 1080px) {
    left: rem(25);
    bottom: rem(220);
  }

  @media (max-width: 1024px) {
    bottom: rem(170);
    left: rem(110);
  }

  @media (max-width: 880px) {
    left: rem(70);
  }

  @media (max-width: 800px) {
    left: rem(40);
  }

  & svg {
    transform: rotate(180deg);
    transition: 0.3s all;
    stroke: $semiGrey;

    &:hover {
      stroke: $lightGrey;
      cursor: pointer;
    }

    @media (max-width: 1080px) {
      width: 35px;
    }
  }
}

.slider__button-next {
  position: absolute;
  right: rem(200);
  bottom: rem(280);

  @media (max-width: 1600px) {
    right: rem(110);
  }

  @media (max-width: 1440px) {
    right: rem(60);
  }

  @media (max-width: 1320px) {
    right: rem(105);
    bottom: rem(240);
  }

  @media (max-width: 1280px) {
    right: rem(55);
  }

  @media (max-width: 1080px) {
    right: rem(30);
    bottom: rem(220);
  }

  @media (max-width: 1024px) {
    bottom: rem(170);
    right: rem(115);
  }

  @media (max-width: 880px) {
    right: rem(75);
  }

  @media (max-width: 800px) {
    right: rem(45);
  }

  & svg {
    transition: 0.3s all;
    stroke: $semiGrey;

    &:hover {
      stroke: $lightGrey;
      cursor: pointer;
    }

    @media (max-width: 1080px) {
      width: 35px;
    }
  }
}

.slider__button-prev.swiper-button-disabled {
  & svg {
    stroke: $lightGrey;

    &:hover {
      cursor: default;
    }
  }
}

.slider__button-next.swiper-button-disabled {
  & svg {
    stroke: $lightGrey;

    &:hover {
      cursor: default;
    }
  }
}

.slider__navigation {
  @media (max-width: 768px) {
    display: none;
  }
}


.img-wrapper img {
  max-width: 100%;
  height: auto;
}

.offset-shadow {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: #FFFFFF;
    box-shadow: 8px 6px 13px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    top: 24px;
    left: 18px;
  }
}

.offset-border {
  position: relative;
  z-index: 0;

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    border: 1px solid #000000;
    border-radius: 5px;
    top: 24px;
    left: 18px;
  }
}

.swiper-pagination-circles {
  display: flex;
  justify-content: center;

  .swiper-pagination-bullet {
    background: #fff !important;
    width: 10px !important;
    height: 10px !important;
    display: inherit;
    border-radius: 50% !important;
  }

  .swiper-pagination-bullet.swiper-pagination-bullet-active {
    background: #545454 !important;
  }

  &--grey {
    .swiper-pagination-bullet {
      background: #E8E8E8 !important;
    }
  }
}

.nav-next,
.nav-prev {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 73px;
  height: 73px;
  background: #373737E5;
  border-radius: 50%;
  cursor: pointer;

  &.swiper-button-disabled {
    cursor: auto;
    opacity: .6;
  }

  &::before {
    content: '';
    position: absolute;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M20.2045 12.7955C19.7652 12.3562 19.7652 11.6438 20.2045 11.2045C20.6438 10.7652 21.3562 10.7652 21.7955 11.2045L27.7955 17.2045C28.2348 17.6438 28.2348 18.3562 27.7955 18.7955L21.7955 24.7955C21.3562 25.2348 20.6438 25.2348 20.2045 24.7955C19.7652 24.3562 19.7652 23.6438 20.2045 23.2045L24.284 19.125H9.75C9.12868 19.125 8.625 18.6213 8.625 18C8.625 17.3787 9.12868 16.875 9.75 16.875H24.284L20.2045 12.7955Z' fill='white'/%3e%3c/svg%3e ");
    width: 36px;
    height: 36px;
    display: inline-block;
  }

  &--small {
    width: 40px;
    height: 40px;
  }
}

.nav-prev {
  &::before {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M15.7965 23.2045C16.2358 23.6438 16.2358 24.3562 15.7965 24.7955C15.3571 25.2348 14.6448 25.2348 14.2055 24.7955L8.20548 18.7955C7.76614 18.3562 7.76614 17.6438 8.20548 17.2045L14.2055 11.2045C14.6448 10.7652 15.3571 10.7652 15.7965 11.2045C16.2358 11.6438 16.2358 12.3562 15.7965 12.7955L11.717 16.875H26.251C26.8723 16.875 27.376 17.3787 27.376 18C27.376 18.6213 26.8723 19.125 26.251 19.125H11.717L15.7965 23.2045Z' fill='white'/%3e%3c/svg%3e ");
  }
}

.breadcrumbs {
  padding-top: 10px;
  &__list {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    font-weight: 300;
    line-height: 16.94px;
    color: #8B8B8B;
  }

  &__item {
    &:nth-child(n + 2) {
      position: relative;

      &::before {
        content: ' > ';
        margin-left: 5px;
      }
    }

    a:hover {
      color: $black;
    }

    span {
      color: $black;
    }
  }
}