.header {
  position: relative;
  background-color: #fff;
  transition: background-color .3s;
  z-index: 99;

  &__wrapper {
    display: flex;
    align-items: center;
    padding: 30px 0;
    transition: background-color .3s;
    @media (max-width: $tablet) {
      padding: 20px 0;
      height: 70px;
    }
  }

  &__logo {
    display: flex;
    position: relative;
    top: -14px;
    margin-right: auto;
    @media (max-width: 1600px) {
      top: -10px;
      max-width: 160px;
    }
    @media (max-width: $laptop) {
      top: -7px;
      max-width: 120px;
    }
    @media (max-width: $tablet) {
      max-width: 152px;
      margin-right: 0;
      order: -2;
    }
    @media (max-width: $mobileMedium) {
      max-width: 34vw;
      margin-left: auto;
      margin-right: auto;
      top: -1vw;
    }
  }

  @media (max-width: $mobileMedium) {
    .icons__item.compare-icon {
      display: none;
    }
    .icons__item.favorite-icon {
      display: none;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    margin-left: auto;
    @media (max-width: $tablet) {
      margin-left: 0;
    }
  }

  &__nav {
    margin-left: auto;
    @media (max-width: $tablet) {
      display: none;
      position: fixed;
      top: 70px;
      left: 0;
      right: 0;
      bottom: 0;
      nav {
        height: 100%;
        width: 100%;
      }
    }
  }

  .nav {
    display: flex;
    align-items: center;

    &__item {
      font-size: 14px;
      font-weight: 400;
      line-height: 18.2px;
      text-transform: uppercase;
      white-space: nowrap;

      a {
        display: inline-block;
        padding: 5px;
        @include hover-effect;
      }

      &--active {
        font-weight: 800;

        a:hover {
          opacity: 1;
        }
      }

      @media (max-width: 1600px) {
        font-size: 13px
      }
      @media (max-width: $laptop) {
        font-size: 12px;
      }

      & + .nav__item {
        margin-left: 20px;
        @media (max-width: 1600px) {
          margin-left: 10px;
        }
        @media (max-width: 1400px) {
          margin-left: 5px;
        }
      }


    }
  }

  &__tel {
    white-space: nowrap;
    @media (max-width: $tablet) {
      order: -1;
      margin-left: auto;
      margin-right: auto;
    }
    @media (max-width: $mobileMedium) {
      margin-left: 15px;
      margin-right: 0;
      order: -3;
    }
  }

  .icons {
    display: flex;
    align-items: center;
    margin: 0 auto;
    margin-right: 30px;
    @media (max-width: 1600px) {
      margin-right: 10px;
    }

    &__item {
      display: flex;
      padding: 5px;
      @include hover-effect;

      & + .icons__item {
        margin-left: 20px;
        @media (max-width: $laptop) {
          margin-left: 10px;
        }
      }
    }
  }

  .pic-link {
    font-size: 20px;
    line-height: 24px;
    @media (max-width: 1600px) {
      font-size: 14px;
      line-height: 20px;
    }

    &__anchor {
      @media (max-width: $laptop) {
        display: none;
      }
      @media (max-width: $tablet) {
        display: block;
      }
      @media (max-width: $mobileMedium) {
        display: none;
      }
    }

    &__icon {
      @media (max-width: $mobileMedium) {
        margin-right: 0;
        max-width: 18px;
      }
    }
  }

  .header-burger-icon {
    display: none;
    @media (max-width: $tablet) {
      display: flex;
      flex-direction: column;
      padding: 6px;
      cursor: pointer;

      span {
        display: inline-block;
        width: 22px;
        height: 1px;
        background-color: #000000;
        transition: transform .3s;

        &:last-child {
          width: 11px;
        }

        & + span {
          margin-top: 5px;
        }
      }
    }
    @media (max-width: $mobileMedium) {
      order: -4;
    }
  }

  &[data-open] {
    @media (max-width: $tablet) {
      background: #D3D3D3;
      transition: background-color .0s;

      .header__nav {
        background: #D3D3D3;
        padding: 0;
      }
      .nav {
        padding: 48px 0;
        height: calc(100vh - 70px);
        flex-direction: column;
        overflow-y: auto;

        &__item {
          font-size: 14px;
          line-height: 16.94px;
          text-align: center;

          & + .nav__item {
            margin-top: 20px;
            margin-left: 0;
          }
        }
      }
      .header-burger-icon {
        span {
          &:last-child {
            display: none;
          }

          &:nth-child(1) {
            transform: rotate(45deg) translate(3px, 4px);
          }

          &:nth-child(2) {
            transform: rotate(-45deg) translate(0.5px, -1.5px);
          }
        }
      }
    }

    @media (max-width: $mobile) {
      .nav {
        padding: 30px 0;
      }
    }

  }

  &--scrolled {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    animation: slide-down .6s;
    box-shadow: 0px 6px 12px 0px #9DA8B266;

    & + main {
      margin-top: var(--header-height);
    }

    .header__wrapper {

    }
  }
}

.pic-link {
  display: flex;
  align-items: center;
  @include hover-effect;

  &__icon {
    display: flex;
    margin-right: 20px;
    @media (max-width: 1600px) {
      margin-right: 10px;
      max-width: 20px;
    }
  }

}