h1, .h1 {
  font-size: 42px;
  font-weight: 300;
  line-height: 50.83px;
  letter-spacing: 0.30000001192092896px;

  @media (max-width: $tablet) {
    font-size: 36px;
    line-height: 43.57px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 29.05px;
    letter-spacing: 0.30000001192092896px;
  }
}

h2, .h2 {
  font-size: 42px;
  font-weight: 300;
  line-height: 50.83px;
  letter-spacing: 0.30000001192092896px;

  @media (max-width: $tablet) {
    font-size: 36px;
    line-height: 43.57px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 29.05px;
    letter-spacing: 0.30000001192092896px;
  }
}

h3,.h3{
  font-size: 24px;
  font-weight: 700;
  line-height: 29.05px;
  letter-spacing: 0.03em;
  @media (max-width: $mobile){
    font-size: 18px;
    line-height: 21.78px;
  }
}

h5,.h5{
  font-size: 16px;
  line-height: 19.36px;
  letter-spacing: 0.3px;
  font-weight: 500;
  @media (max-width: $mobile) {
    font-size: 14px;
    line-height: 19px;
  }
}

h6, .h6{
  font-size: 15px;
  line-height: 18.36px;
  letter-spacing: 0.3px;
  font-weight: 500;
  @media (max-width: $mobile) {
    font-size: 13px;
    line-height: 15px;
  }
}