.footer {
  &__logo-box {
    display: flex;
    align-items: center;
    width: 45%;
    flex-shrink: 0;
    padding: 60px;
    @media (max-width: $laptop) {
      width: 35%;
    }
    @media (max-width: $tablet) {
      width: 100%;
      justify-content: center;
      padding: 0;
      margin-bottom: 50px;
    }
    @media (max-width: $mobileMedium) {
      margin-bottom: 30px;
    }
  }

  &__logo {
    max-width: 100%;
    @include hover-effect;

    @media (max-width: $mobileMedium) {
      max-width: 170px;
    }

    svg {
      max-width: 100%;
    }
  }


  &__contacts {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    flex-grow: 1;
    @media (max-width: $tablet) {
      width: calc(50% - 30px);
      flex-grow: 0;
    }
    @media (max-width: $mobileMedium) {
      width: 100%;
      font-size: 14px;
      line-height: 16.94px;
    }

  }

  &__bottom {
    border-top: 1px solid #EAEAEA;
  }

  &__copyright-links {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 15px;
    padding: 10px 0;
    color: #4A4A4AB2;
    @media (max-width: $mobileMedium) {
      text-align: center;
      align-items: center;
      justify-content: center;
    }

    a:last-child,
    span:last-child {
      margin: 0 auto;
    }

  }

  &__content {
    padding: 70px 0;
    display: flex;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      padding: 40px 0;
      justify-content: space-between;
    }
    @media (max-width: $mobileMedium) {
      padding: 30px 0;
    }
  }

  &__nav {
    font-size: 16px;
    font-weight: 400;
    line-height: 19.36px;
    color: #454545;
    margin-right: 10%;
    @media (max-width: $tablet) {
      width: calc(50% - 30px);
      margin-right: 0;
    }
    @media (max-width: $mobileMedium) {
      width: 100%;
      margin-bottom: 30px;
      font-size: 14px;
      line-height: 16.94px;
    }

    li + li {
      margin-top: 6px;
    }

    a {
      display: block;
      padding: 5px;
      @include hover-effect;
    }
  }
}

.footer-contact {
  display: flex;
  align-items: center;

  & + .footer-contact {
    margin-top: 30px;
  }

  &__icon {
    margin-right: 20px;
  }

  &__value {
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    @media (max-width: $mobileMedium) {
      font-size: 14px;
      line-height: 16.94px;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__name {
    font-size: 16px;
    font-weight: 600;
    line-height: 19.6px;
    margin-bottom: 1px;
    @media (max-width: $mobileMedium) {
      font-size: 14px;
      line-height: 16.94px;
    }
  }
}

a.footer-contact {
  .footer-contact__value {
    white-space: nowrap;
  }
}