.form-input {
  position: relative;
  display: inline-block;
  width: 100%;

  &--search {

  }

  &--agree {
    font-family: "Raleway";
    @media (max-width: $mobileMedium){
      font-size: 10px;
      font-weight: 300;
      line-height: 11.74px;
    }
  }


  &__error-text {
    position: relative;
    z-index: 1;
    //top: calc(100% + 4px);
    left: 0;
    font-size: 12px;
    color: #FF5252;
    line-height: 14px;
  }

  &:not(.form-input--error) {
    input,
    textarea {
      &:hover,
      &:active {
        border: 1px solid #D8DBE2;
      }

      &[disabled]:hover {
        border: 1px solid #C9C9C9;
      }
    }
  }


  input,
  textarea {
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    width: 100%;
    border: 1px solid #C9C9C9;
    padding: 12px 17px;
    border-radius: 5px;
    background: #FFFFFF66;
    color: #3A3A3A;


    &::placeholder {
      color: #666666;
    }
  }

  textarea {
    resize: none;
  }

  &--m {
    input,
    textarea {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      padding: 9.5px 15px;
    }
  }

  &:not(.form-input--error)[data-focused] {
    &::after {
      content: '';
      position: absolute;
      right: -2px;
      top: -2px;
      bottom: -2px;
      left: -2px;
      border: 1px solid #D8DBE2;
      border-radius: 5px;
    }

    input,
    textarea {
      border: 1px solid #C9C9C9;
    }
  }

  label {
    display: inline-block;
    color: #000000;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 8px;
  }

  input[type='radio'] {
    display: none;

    & + label {
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 35px;
      cursor: pointer;

      @media (max-width: $mobile) {
        font-size: 16px;
        line-height: 20px;
      }

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        border: 1px solid #101010;
        border-radius: 50%;
        margin-right: 15px;
      }
    }

    &:checked + label {
      &::before {
        background-color: #1EB2E8;
        border-color: #1EB2E8;
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 5px;
        width: 10px;
        transform: translateY(-50%);
        height: 10px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 15px;
      }
    }
  }

  input[type='checkbox'] {
    display: none;

    & + label {
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;
      color: #777777;
      font-size: 13px;
      font-weight: 300;
      line-height: 15.26px;
      margin-bottom: 0;


      &::before {
        content: '';
        width: 20px;
        height: 20px;
        border: 1px solid #777777;
        border-radius: 5px;
        margin-right: 16px;
        flex-shrink: 0;
      }
    }

    &:checked + label {
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 3px;
        display: inline-block;
        width: 14px;
        height: 14px;
        background-position: center;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill='%23111' d='M3 6.732l.79-.79 3.274 3.273-.79.791z'/%3e%3cpath fill='%23111' d='M11.216 3.482l.79.79-5.732 5.734-.79-.79z'/%3e%3c/svg%3e");
        background-repeat: no-repeat;
        background-size: cover;
      }
    }
  }

  &--error {
    input[type='checkbox'] {
      & + label {
        color: #FF5252;

        &::before {
          border-color: #FF5252;
        }
      }
    }
  }

  &__search-submit {
    position: absolute;
    display: flex;
    padding: 10px;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }

  &--error {
    input,
    textarea {
      border-color: #FF5252;
    }
  }

  &--success {
    .form-input__error-text {
      display: none;
    }
  }

  label {
    margin-bottom: 8px;
  }
}


.form-body {
  &__fields-wrapper {
    background-color: #fff;
    padding: 30px;
    border-radius: 30px;
    @media (max-width: $tablet) {
      border-radius: 5px;
      padding: 16px;
    }
    @media (max-width: $mobile) {
      padding: 12px;
    }
  }

  &__field {
    label {
      display: block;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      margin-bottom: 6px;
    }
  }

  &__two-in-row {
    display: flex;
    margin: 0 -15px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0;
    }

    &:last-child {
      margin-bottom: -40px;
      @media (max-width: $tablet) {
        margin-bottom: -16px;
      }
    }

    .form-body__field {
      width: calc(50% - 30px);
      margin: 0 15px 40px;
      @media (max-width: $tablet) {
        width: 100%;
        margin: 0 0 16px;
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    margin-top: 24px;

    .btn {
      & + .btn {
        margin-left: 12px;
      }
    }
  }

  &__results {
    display: none;
    position: absolute;
    margin-top: 12px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    @media (max-width: $tablet) {
      margin-top: 8px;
      font-size: 14px;
      line-height: 16px;
    }
    @media (max-width: $mobile) {
      font-size: 12px;
      line-height: 14px;
    }

    &[data-success] {
      display: block;
      color: #1EB2E8;
    }

    &[data-error] {
      display: block;
      color: #FF5252;
    }
  }
}

.password-eye {
  position: absolute;
  right: 20px;
  top: 25px;
  transform: translateY(-50%);
  cursor: pointer;
  display: inline-block;
  width: 20px;
  height: 12px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='12' viewBox='0 0 20 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M10 0C5.68182 0 1.99409 2.488 0.5 6C1.99409 9.512 5.68182 12 10 12C14.3182 12 18.0059 9.512 19.5 6C18.0059 2.488 14.3182 0 10 0ZM10 10C7.61636 10 5.68182 8.208 5.68182 6C5.68182 3.792 7.61636 2 10 2C12.3836 2 14.3182 3.792 14.3182 6C14.3182 8.208 12.3836 10 10 10ZM10 3.6C8.56636 3.6 7.40909 4.672 7.40909 6C7.40909 7.328 8.56636 8.4 10 8.4C11.4336 8.4 12.5909 7.328 12.5909 6C12.5909 4.672 11.4336 3.6 10 3.6Z' fill='%23E0E4EA'/%3e%3c/svg%3e ");
}

.form {
  .form__some {
    display: flex;
    margin-top: 16px;

    .form-input {
      margin: 0 6px 12px;

      & + .form-input {
        margin-top: 0;
      }
    }
  }
}

.form-select {
  .choices {
    &__inner {
      border: 1px solid #E9ECF2;
      border-radius: 5px;
      background: #fff;
      overflow: hidden;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
    }

    &[data-type*="select-one"].is-open::after {
      border-color: transparent transparent #E9ECF2;
    }

    &[data-type*="select-one"] {
      border-radius: 5px;
    }

    .choices__list {
      .choices__item {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;

        &.is-selected {
          color: #1EB2E8;
        }
      }
    }

    &[data-type*="select-one"]::after {
      margin-top: 0;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.48682 9.84205C7.85106 9.50383 8.42051 9.52492 8.75874 9.88916L11.9992 13.3789L15.2397 9.88916C15.5779 9.52492 16.1474 9.50383 16.5116 9.84205C16.8759 10.1803 16.897 10.7497 16.5587 11.114L12.6587 15.314C12.4884 15.4974 12.2495 15.6016 11.9992 15.6016C11.749 15.6016 11.51 15.4974 11.3397 15.314L7.43971 11.114C7.10149 10.7497 7.12258 10.1803 7.48682 9.84205Z' fill='%239CA5AD'/%3e%3c/svg%3e ");
      border: none;
      width: 24px;
      height: 24px;
      background-repeat: no-repeat;
      background-position: center;
      transform: translateY(-50%);
      transition: all .3s;
    }

    &[data-type*="select-one"].is-open::after {
      margin-top: 0;
      transform: translateY(-50%) rotate(180deg);
    }

    &__list--dropdown,
    &__list[aria-expanded] {
      border: 1px solid #E9ECF2;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
    }

    &.is-open .choices__inner {
      border-radius: 15px 15px 0 0;
    }

    &.is-focused .choices__inner,
    &.is-open .choices__inner {
      border-color: #E9ECF2;
    }

    &.is-open .choices__list--dropdown,
    &.is-open .choices__list[aria-expanded] {
      border-color: #E9ECF2;
    }
  }
}

.form-radio-box {
  display: inline-block;
  cursor: pointer;

  input {
    display: none;

    &:checked + label {
      border: 1px solid #1EB2E8;
      background: #E8F9FF;
      color: #1EB2E8;

      &:hover {
        opacity: 1;
      }
    }
  }

  label {
    display: inline-block;
    padding: 10px 12px;
    background-color: #fff;
    cursor: pointer;
    border: 1px solid #E9ECF2;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @include hover-effect;
    @media (max-width: $mobile) {
      padding: 8px 10px;
      font-size: 14px;
      line-height: 21px;
    }
  }
}

.double-fields {
  display: flex;
  justify-content: space-between;
  margin: 0 -15px;
  @media (max-width: $mobileMedium) {
    flex-wrap: wrap;
    margin: 0;
  }

  .form-input {
    width: calc(50% - 30px);
    margin: 0 15px;
    @media (max-width: $mobileMedium) {
      width: 100%;
      margin: 0;
    }
  }
}