.breadcrumbs {
  font-size: 1.4rem;
  line-height: 1.7rem;
  letter-spacing: 0.03rem;
  padding: 1rem 0;
}

.brief {
  padding: 60px 0;
  @media (max-width: $tablet) {
    padding: 50px 0;
  }
  @media (max-width: $mobileMedium) {
    padding: 25px 0;
  }

  &__header {
    margin-bottom: 1rem;
    font-weight: 700;
    font-size: 1.2rem;
    line-height: 1.5rem;
    letter-spacing: 0.03rem;
    text-transform: uppercase;
    color: #363636;

    svg {
      margin-right: 0.5rem;
    }
  }

  &__welcome {
    margin-bottom: 4rem;
  }
}

.brief-header {
  display: flex;

  &__left, &__right {
    display: flex;
    align-items: center;
  }

  &__left {
    margin-right: 4rem;
  }
}

.card-welcome {
  display: grid;
  grid-template-columns: 75% 1fr;
  align-items: center;

  @media (max-width: $mobileMedium) {
    .mini-gallery {
      .swiper {
        &::after {
          content: '';
          background: linear-gradient(269.64deg, #FFFFFF 31.16%, rgba(255, 255, 255, 0) 129.37%);
          height: 100%;
          width: 50px;
          right: 0;
          top: 0;
          z-index: 1;
          display: block;
          position: absolute;
        }
      }
    }
    &.reached-end {
      .swiper {
        &::after {
          content: none;
        }
      }
    }
  }

  &__props {
    @media (max-width: $tablet) {
      display: flex;
    }
    @media (max-width: $mobile) {
      flex-direction: column;
    }
  }

  &__left {
    position: relative;
    height: 100%;
    @media (max-width: $tablet) {
      grid-column: 1/3;
    }
  }

  &__slider {
    height: 100%;
  }

  &__right {
    padding-left: 4rem;
    @media (max-width: $tablet) {
      grid-row: 4;
      padding-left: 0;
      display: flex;
      grid-column: 1/3;
      margin-top: 40px;
    }
    @media (max-width: $mobileMedium) {
      margin-top: 80px;
    }
  }

  &__mini-gallery {
    grid-column: 1/3;
    margin-top: 40px;
    @media (max-width: $tablet) {
      margin-top: 20px;
    }
    @media (max-width: $mobile) {
      margin-top: 10px;
    }
  }

  &__prev, &__next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    cursor: pointer;

    &.swiper-button-disabled {
      opacity: .5;
      cursor: auto;
    }
  }

  &__prev {
    left: 2rem;
  }

  &__next {
    right: 2rem;
  }


  &__item {
    @media (max-width: $tablet) {
      width: calc(33% - 40px);
      margin: 0 20px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      text-align: center;
    }
    @media (max-width: $mobile) {
      width: 100%;
      flex-direction: row;
      text-align: left;
      flex-wrap: wrap;
      margin: 0;
    }

    & + .card-welcome__item {
      @media (max-width: $mobile) {
        margin-top: 30px;
      }
    }

    &:not(:last-child) {
      margin-bottom: 4rem;
      @media (max-width: $tablet) {
        margin-bottom: 0;
      }
    }
  }

  &__icon {
    margin-bottom: 1.5rem;
    @media (max-width: $mobile) {
      margin-bottom: 5px;
      margin-right: 5px;
    }
  }

  &__caption {
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 1.9rem;
    letter-spacing: 0.03rem;
    text-transform: uppercase;
    color: #232323;
    margin-bottom: 1rem;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 16px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 2.2rem;
    letter-spacing: 0.03rem;
    color: #363636;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 19.36px;
    }
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 16px;
      width: 100%;
    }
  }

  &__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    text-align: center;

    .section-header__subtitle {
      color: #FFFAFA;
    }

    .section-header__title {
      color: #FFFFFF;
    }
  }
}

.card-slider {
  &__slide {
    width: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.mini-gallery {
  position: relative;
  display: flex;


  .swiper-pagination-circles {
    display: none;
    @media (max-width: $mobileMedium) {
      display: flex;
      position: absolute;
      bottom: -40px;
    }
  }

  &__item {
    width: 100%;
    height: 20.5rem;
    cursor: pointer;
    @media (max-width: $tablet) {
      height: 10rem;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}


.project-description {
  padding-top: 60px;
  @media (max-width: $tablet) {
    padding-top: 50px;
  }
  @media (max-width: $mobile) {
    padding-top: 25px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.9rem;
    letter-spacing: 0.25rem;
    text-transform: uppercase;
    color: #939090;
    margin-bottom: 0.5rem;
  }

  &__title {
    font-weight: 300;
    font-size: 4.2rem;
    line-height: 5.1rem;
    letter-spacing: 0.03rem;
    text-transform: uppercase;
    margin-bottom: 3rem;
  }

  &__paragraph {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.5rem;
    letter-spacing: 0.03rem;
    color: #363636;
    @media (max-width: $mobile) {
      font-size: 14px;
      font-weight: 400;
      line-height: 19px;
    }

    p {
      &:not(:last-child) {
        margin-bottom: 1rem;
      }
    }
  }

  &__banner {
    margin-top: 6rem;
    @media (max-width: $laptop) {
      margin-top: 40px;
    }
  }

  &__sides {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 60px 0;
    @media (max-width: $laptop) {
      margin: 40px 0;
    }

    &:not(.project-description__sides--photos) {
      @media (max-width: $tablet) {
        flex-wrap: wrap;
        .project-description__left,
        .project-description__right {
          width: 100%;
        }
        .project-description__right {
          margin-top: 40px;
        }
      }
    }
  }

  &__left,
  &__right {
    width: calc(50% - 35px);
    @media (max-width: $laptop) {
      width: calc(50% - 20px);
    }
    @media (max-width: $mobile) {
      width: calc(50% - 10px);
    }
  }

  &__left {
    flex-shrink: 0;

    img {
      width: 100%;
      height: auto;
    }
  }

  &__right {
    flex-shrink: 0;


    img {
      width: 100%;
      height: auto;
    }
  }
}

.banner-descr {
  position: relative;

  &__text {
    position: absolute;
    transform: translateX(-50%) translateY(50%);
    bottom: 0;
    left: 50%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    z-index: 2;
    width: 80%;
    min-height: 70px;
    @media (max-width: $mobileMedium) {
      width: 90%;
    }
  }

  &__img {
    width: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.preview-filter {
  display: flex;
  align-items: center;
  @media (max-width: $tablet) {
    display: none;
  }

  &__caption {
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 1.9rem;
    letter-spacing: 0.03rem;
    text-transform: uppercase;
    color: #232323;
    margin-right: 2.5rem;
  }

  &__button {
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: #000000;

    &:not(:last-child) {
      margin-right: 3rem;
    }

    &--active {
      background: #DBDBDB;
      border: 1px solid #DBDBDB;
      border-radius: 0.5rem;
      padding: 0.6rem 2rem;
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 1.9rem;
      letter-spacing: 0.03rem;
      text-transform: uppercase;
      color: #232323;
    }
  }

  &__wrapper {
    display: flex;
    overflow-x: auto;
  }
}

.goods {
  margin-bottom: 120px;
  @media (max-width: $tablet) {
    margin-bottom: 100px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 60px;
  }

  .section-header {
    text-align: center;
  }

  &__wrapper {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      background: linear-gradient(269.64deg, #FFFFFF 31.16%, rgba(255, 255, 255, 0) 80.37%);
      height: 100%;
      width: calc(50vw - 50%);
      margin-right: calc(50% - 50vw);
      top: 0;
      right: 0;
      z-index: 2;
      @media (max-width: $mobileMedium) {
        content: none;
      }
    }
  }

  &__slider {
    position: relative;
  }

  &__filter {
    margin-bottom: 3rem;
  }

  &__next, &__prev {
    position: absolute;
    cursor: pointer;
    top: 50%;
    transform: translate(50%, -50%);
    z-index: 3;
    @media (max-width: $mobileMedium) {
      display: none;
    }

    &.swiper-button-disabled {
      opacity: .5;
      cursor: auto;
    }
  }

  &__prev {
    left: -3%;
    transform: translate(-50%, -50%) rotate(180deg);
    @media (max-width: $mobile) {
      left: 3%;
    }
  }

  &__next {
    right: -3%;
    @media (max-width: $mobile) {
      right: 3%;
    }
  }

  .swiper-button-disabled {
    display: none;
  }
}


.separator {
  text-align: center;
  background: linear-gradient(180deg, #F2F2F2 0%, rgba(242, 242, 242, 0.2) 100%);

  &.img-center {
    padding-top: 5rem;
  }

  img {
    mix-blend-mode: multiply;
    max-width: 100%;
    height: auto;
  }
}


.goods-slider {
  position: relative;
  @media (max-width: $mobileMedium) {
    padding-bottom: 20px;
  }

  .swiper {
    margin: 0 calc(50% - 50vw);
    @media (max-width: $mobileMedium) {
      margin: 0;
    }
  }

  .swiper-pagination-circles {
    display: none;
    @media (max-width: $mobileMedium) {
      display: flex;
      position: absolute;
      bottom: -10px;
    }
  }

  .swiper-slide {
    width: 318px;
    @media (max-width: $tablet) {
      width: 27vw;
    }
  }
}



.portfolio-items {

  &__img {
    margin-bottom: 1rem;
    height: 27.1rem;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__category {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3rem;

    svg {
      margin-right: 0.5rem;
    }

    span {
      font-weight: 700;
      font-size: 1.2rem;
      line-height: 1.5rem;
      letter-spacing: 0.03rem;
      text-transform: uppercase;
      color: #363636;
    }
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
  }

  &__title {
    font-weight: 800;
    font-size: 1.8rem;
    line-height: 2.2rem;
    text-transform: uppercase;
    color: #000000;
  }
}