        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        background-position: center;
        background-color: #DBDBDB;
        border-radius: 5px;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='8' height='8' viewBox='0 0 8 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M2.9887 8V0L5.0113 0V8H2.9887ZM0 5.0113L0 2.9887H8V5.0113H4H0Z' fill='black'/%3e%3c/svg%3e ");
      }
