      @media (max-width: $tablet) {
        width: 42vw;
      }
    }

    &:after {
      content: '';
      position: absolute;
      background: linear-gradient(269.64deg, #FFFFFF 31.16%, rgba(255, 255, 255, 0) 80.37%);
      height: 100%;
      width: 40rem;
      top: 0;
      right: 0;
