      margin: 0 20px 50px;
      @media (max-width: $tablet) {
        width: calc((100% / 2) - 20px);
        margin: 0 10px 40px;
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 35px;
      }
    }
  }
}


.capabilities {
  padding: 60px 0;
  @media (max-width: $tablet) {
    padding: 50px 0;
  }
  @media (max-width: $mobileMedium) {
    padding: 25px 0;
  }

  &__items {
    @media (max-width: $tablet) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: $tablet) {
      flex-direction: column-reverse;
    }
  }

  &--reverse {
    .capabilities {
      &__wrapper {
        flex-direction: row-reverse;
        @media (max-width: $tablet) {
          flex-wrap: wrap;
        }
      }

      &__photo {
        margin-left: 0;
        margin-right: 100px;
        @media (max-width: $tablet) {
          margin-right: 0;
          width: 100%;
          margin-bottom: 60px;
          order: -1;
        }
        @media (max-width: $mobile) {
          margin-bottom: 60px;
        }

      }
    }
  }

  &--reverse-tablet {
    @media (max-width: $tablet) {
      .capabilities {
        &__info {
          margin-bottom: 40px;

          .section-header {
            margin-bottom: 0;
          }
        }

        &__wrapper {
          flex-direction: column;
        }
      }
    }
  }

  &__info {
    flex-grow: 1;
    @media (max-width: $tablet) {
      width: 100%;
    }

    .section-header {
      margin-bottom: 20px;

      &__title,
      &__subtitle {
        text-align: left;
      }
    }
  }

  &__description {
    text-align: left;
  }

  .section-header {
    text-align: center;
    justify-content: center;
  }

  &__photo {
    width: 57%;
    position: relative;
    margin-left: 100px;
    @media (max-width: $tablet) {
      width: 100%;
      margin-left: 0;
      margin-bottom: 40px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
      width: 100%;
    }

    &--slider {
      @media (max-width: $mobile) {
        margin-bottom: 60px;
      }
    }
  }


  &__img {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__text {
    position: absolute;
    transform: translateX(-50%) translateY(50%);
    bottom: 0;
    left: 50%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    z-index: 2;
    width: 80%;
    min-height: 70px;
    @media (max-width: $mobileMedium) {
      width: 90%;
    }
  }
}

.capabilities-slider {
  &__prev, &__next {
    cursor: pointer;
    position: absolute;
    z-index: 3;
    top: 50%;
    transform: translateY(-50%);

    &.swiper-button-disabled {
      opacity: .5;
      cursor: auto;
    }
  }

  &__prev {
    left: 2.3rem;
    @media (max-width: $mobile) {
      left: 10px;
    }
  }

  &__next {
    right: 2.3rem;
    @media (max-width: $mobile) {
      right: 10px;
    }
  }

  &__img {
    width: 100%;

    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
}

.capability {
  &__item {
    display: flex;
    align-items: center;
    @media (max-width: $tablet) {
      width: calc(50% - 10px);
    }
    @media (max-width: $mobile) {
      width: 100%;
    }

    &:not(:last-child) {
      margin-bottom: 3rem;
      @media (max-width: $tablet) {
        margin-bottom: 30px;
      }
      @media (max-width: $mobile) {
        margin-bottom: 15px;
      }

    }
  }

  &__icon {
    width: 4rem;
    height: 4rem;
    margin-right: 3rem;
  }
}

.features {
  &__wrapper {
    display: flex;
    margin: 0 -20px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0 -10px -40px;
    }
    @media (max-width: $mobile) {
      margin: 0 0 -30px;
    }

    .feature-item {
      width: calc(25% - 40px);
      margin: 0 20px;
      flex-grow: 1;
      @media (max-width: $tablet) {
        width: calc(50% - 20px);
        margin: 0 10px 40px;
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 30px;
      }
    }
  }
}

.feature-item {
  &__title-block {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }

  &__list {
    font-size: 16px;
    font-weight: 400;
    line-height: 19.36px;
    letter-spacing: 0.30000001192092896px;
    padding-left: 20px;

    li {
      list-style-type: disc;

      & + li {
        margin-top: 10px;
      }
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
  }

  &__name {
    text-transform: uppercase;
    font-weight: 500;
  }
}

